.copy-button {
    @extend .btn-link;
    &::before {
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        content: "\f0c5";
    }
}
.copy-button.copied {
    &::before {
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        content: "\f00c";
        color: green;
    }
}
